import React from 'react'
import { useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SwipeableViews from 'react-swipeable-views';
import { useState } from 'react';
import TabPanel from './TabPanel';
import styled from 'styled-components';
import { selectCart, selectFav } from '../features/userSlice';
import { useSelector } from 'react-redux';
import { useEffect } from 'react';
import { db } from './firebase';



function a11yProps(index) {
    return {
      id: `full-width-tab-${index}`,
      'aria-controls': `full-width-tabpanel-${index}`,
    };
  }

export default function CartFav() {
    const theme = useTheme();
    const [value, setValue] = useState(0)
    const cart = useSelector(selectCart)
    const fav = useSelector(selectFav)
    const [items, setItems] = useState([])


    useEffect(() => {
        db.collection('knives').orderBy('timestamp', 'asc').onSnapshot(snap => (
            setItems(snap.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data()
                }
            ))),
            setItems(items.filter(x => x.id && cart.includes(x.id)))
        ))
        return
    }, [items])


    const handleChange = (event, newValue) => {
        setValue(newValue);
      };
    
      const handleChangeIndex = (index) => {
        setValue(index);
      };

    return (
        <Container>
            <AppBar position="static" color="default">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    variant="fullWidth"
                >
                <Tab label="Coul de cumprturi" {...a11yProps(0)} />
                <Tab label="Favorite" {...a11yProps(1)} />
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel value={value} index={0} dir={theme.direction}>
                    {items.map((item, id) => (
                        <ItemCard key={id}>
                            <p>{item.data.name}</p>
                        </ItemCard>
                    ))}
                </TabPanel>
                <TabPanel value={value} index={1} dir={theme.direction}>
                    tem Two
                </TabPanel>
            </SwipeableViews>
        </Container>
    )
}



const Container = styled.div`
    margin: 5px;
    width: 100%;
    .MuiTab-textColorPrimary.Mui-selected {
        color: var(--c1);
    }
    .PrivateTabIndicator-colorPrimary-2 {
        background-color: var(--c1) !important;
    }
`
const ItemCard = styled.div`

`